import { animate, style, transition, trigger } from '@angular/animations';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'ntx-card-basic',
  templateUrl: './card-basic.component.html',
  styleUrls: ['./card-basic.component.scss']
})
export class CardBasicComponent implements OnInit {

	@Input() label: string = "";
	@Input() title: string = "";
	@Input() background: string = "https://via.placeholder.com/468x292?text=+";
	@Input() state: string = "";
	@Input() progress: number = 0;

	@Output() clickEvent = new EventEmitter();

	isLoading = false;

	constructor() { 
		setTimeout(() => {
			this.isLoading = false;
		}, 3000);
	};

	ngOnInit() { }

	public onClickEvent(i) {
		this.clickEvent.emit();
	}

}